//  request 
let ajaxTimes = 0

// 
// const baseUrl = 'https://api.it120.cc/llg'
const baseUrl = 'https://api.it120.cc/doufu'
var Timer = null

function request(params) {
	//  request  ajaxTimes 
	ajaxTimes++
	// 
	// Timer = setTimeout(() => {
		// uni.showLoading({
		// 	title: '...',
		// 	mask: true
		// })
	// }, 0)
	return new Promise(function(resolve, reject) {
		uni.request({
			...params,
			url: baseUrl + params.url,
			method: params.method,
			timeout: '10000',
			data: params.data,
			header: {
				'Content-Type': 'application/x-www-form-urlencoded'
			},
			success(res) {
				resolve(res)
			},
			fail(err) {
				reject(err)
			},
			complete() {
				// console.log(Timer)
				// clearTimeout(Timer)
				//  ajaxTimes 
				ajaxTimes--

				if (ajaxTimes === 0) {
					// 
					
					// uni.hideLoading()
				}
			}
		})
	})
}


export {
	request
}
